@import './root-styles.css';
@import './root-utils.css';
@import './root-typography.css';
@import './utils.css';

/* STATIC STYLES (desktop-first) */

.app {
  display: flex;
  flex-direction: column;

  min-height: 100vh;
}

main {
  flex: 1;
}

section:not(:first-child) {
  margin: 0 auto;
  padding: 10rem 12rem;
}

.section-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  opacity: 0;

  transform: translateX(-100px);
  animation: fadeInUp linear forwards;

  animation-range: entry 25% cover 30%;
  animation-timeline: view();
  gap: 1rem;
}

@keyframes fadeInUp {
  to {
    opacity: 1;

    transform: translateX(0);
  }
}

@supports not (animation-timeline: view()) {
  .section-header {
    opacity: 1;

    transform: translateX(0);
    animation: none;
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
  section:not(:first-child) {
    padding: 8rem 9rem;
  }
}

@media (max-width: 992px) {
  section:not(:first-child) {
    padding: 6rem 6rem;
  }
}

@media (max-width: 768px) {
  section:not(:first-child) {
    padding: 4rem 4rem;
  }
}

@media (max-width: 576px) {
  section:not(:first-child) {
    padding: 4rem 2rem;
  }
}
